*, :before, :after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (max-width: 22em) {
  html {
    font-size: 50%;
  }
}

body {
  min-height: 100vh;
  color: #222;
  background: #f4f1de;
  place-items: center;
  padding: 3rem 3rem 3rem 5.5rem;
  font-family: Roboto Mono, monospace;
  font-size: 1.7rem;
  font-weight: 500;
  display: grid;
  overflow-x: hidden;
}

.game {
  border-radius: 2px;
  grid-template-rows: 7rem minmax(35rem, min-content) 7rem;
  grid-template-columns: minmax(min-content, 90rem);
  margin-bottom: 2rem;
  display: grid;
  box-shadow: -25px 25px #233d4d;
}

.game__header {
  background: #e07a5f;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

.game__header .logo, .game__header .author-logo {
  white-space: nowrap;
  font-family: Barrio, cursive;
}

.game__header .author-logo a:link, .game__header .author-logo a:visited {
  color: inherit;
  text-decoration: none;
}

.game__header .timer {
  color: #fbf8cc;
  padding: 0 1rem;
}

.game__main {
  background: #f2cc8f;
  grid-template-rows: minmax(17.5rem, min-content) 17.5rem;
  grid-template-columns: 1fr;
  display: grid;
}

.game__main .quote-field, .game__main .typing-field {
  place-items: center;
  padding: .5rem 2rem;
  display: grid;
}

.game__main .quote-field .quote {
  max-width: 50rem;
  width: 100%;
  overflow-y: unset;
  padding-top: 1rem;
}

.game__main .quote-field .quote-author {
  opacity: .5;
  text-align: end;
  place-self: end;
  padding-top: 2rem;
}

.game__main .typing-field textarea {
  font: inherit;
  resize: none;
  height: 80%;
  max-width: 50rem;
  width: 100%;
  background: #f4f1de;
  border: none;
  border-radius: 2px;
  outline: none;
  padding: 1rem;
  box-shadow: -5px 5px #233d4d;
}

.game__results {
  color: #fbf8cc;
  background: #81b29a;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  column-gap: 1rem;
  padding: 0 1rem;
  font-weight: 400;
  display: flex;
}

.game__results span {
  color: #222;
  font-weight: 700;
}

.correctStyle {
  color: #81b29a;
}

.incorrectStyle {
  color: #e07a5f;
}

